.color-tool-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 24px;
}
.color-tool-container .color-main-card {
  border-radius: 16px;
  box-shadow: 0 6px 24px rgba(0, 0, 0, 0.06);
}
.color-tool-container .color-main-card .ant-card-head {
  border-bottom: 1px solid #f0f0f0;
  padding: 20px 24px;
}
.color-tool-container .color-main-card .header-container {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.color-tool-container .inner-card {
  background: #fafafa;
  border-radius: 12px;
  height: 100%;
}
.color-tool-container .inner-card .ant-card-head {
  min-height: 48px;
  padding: 0 16px;
  font-size: 15px;
}
.color-tool-container .inner-card .ant-card-body {
  padding: 16px;
}
.color-tool-container .tab-content {
  padding-top: 16px;
}
.color-tool-container .picker-wrapper {
  display: flex;
  justify-content: center;
  padding: 24px 0;
}
.color-tool-container .picker-wrapper .ant-color-picker-panel {
  border: none;
  box-shadow: none;
}
.color-tool-container .preview-box {
  height: 120px;
  border-radius: 12px;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.05);
  background-image: linear-gradient(45deg, #eee 25%, transparent 25%), linear-gradient(-45deg, #eee 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #eee 75%), linear-gradient(-45deg, transparent 75%, #eee 75%);
  background-size: 20px 20px;
  background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
}
.color-tool-container .gradient-pickers {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  background: #fff;
  border-radius: 8px;
  border: 1px solid #f0f0f0;
}
.color-tool-container .palette-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 16px;
  margin-top: 24px;
}
.color-tool-container .palette-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}
.color-tool-container .palette-item .color-swatch {
  width: 100%;
  aspect-ratio: 1;
  border-radius: 12px;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}
.color-tool-container .palette-item .color-swatch:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
}
.color-tool-container .palette-item .color-swatch:hover .copy-hint {
  opacity: 1;
}
.color-tool-container .palette-item .color-swatch .copy-hint {
  opacity: 0;
  color: white;
  background: rgba(0, 0, 0, 0.4);
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  transition: opacity 0.2s;
  backdrop-filter: blur(4px);
}
.color-tool-container .contrast-preview {
  padding: 32px;
  border-radius: 12px;
  text-align: center;
  min-height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 16px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  transition: all 0.3s;
}
.color-tool-container .contrast-stats {
  display: flex;
  justify-content: space-around;
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid #f0f0f0;
}
.color-tool-container .contrast-stats .stat-item {
  text-align: center;
}
.color-tool-container .contrast-stats .stat-item .ratio-value {
  font-size: 32px;
  font-weight: 700;
  line-height: 1.2;
  margin-top: 8px;
}
@media (max-width: 768px) {
  .color-tool-container {
    padding: 12px;
  }
  .color-tool-container .color-main-card .ant-card-head {
    padding: 16px;
  }
  .color-tool-container .color-main-card .ant-card-body {
    padding: 12px;
  }
  .color-tool-container .gradient-pickers {
    flex-direction: column;
    gap: 12px;
  }
  .color-tool-container .gradient-pickers > .ant-space {
    width: 100%;
    justify-content: center;
  }
  .color-tool-container .contrast-stats {
    flex-direction: column;
    gap: 16px;
  }
  .color-tool-container .contrast-stats .stat-item {
    padding-bottom: 16px;
    border-bottom: 1px solid #f0f0f0;
  }
  .color-tool-container .contrast-stats .stat-item:last-child {
    padding-bottom: 0;
    border-bottom: none;
  }
}
